body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.container {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
}

.active {
  color: #2258A2;
  border-bottom: 2px solid #2258A2;
  padding-bottom: 5px;
}

.header {
  width: 100%;
  box-shadow: 0px 3px 5px 5px rgba(0, 0, 0, 0.137);
  position: relative;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-nav {
  width: 100%;
}

.header-nav ul {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 0;
}

.header-nav ul li {
  list-style: none;
  margin: 0 auto;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 600;
}

.header_logo {
  padding: 5px 0;
}

.header_language {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #2258A2;
  width: 80px;
  height: 60px;
  color: #fff;
}

.header_mobile {
  display: none;
  position: absolute;
  top: 70px;
  transition: all 0.3s;
}

.header_mobile_btn {
  display: none;
}

.main .current_page {
  margin: 50px 0;
  text-transform: uppercase;
  display: flex;
}

.main .current_page .main_page {
  color: #7D8182;
  border-left: 5px solid #2258A2;
  padding-left: 10px;
}

.main .current_page .current {
  color: #000;
}

.main .content .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.main .content .container p {
  max-width: 500px;
}

.main .content .container .title {
  font-size: 50px;
  color: #34495E;
  font-weight: 600;
}

.main .content .container .img {
  width: 600px;
  height: 500px;
}

.main .content .container .img img {
  width: 100%;
  object-fit: contain;
}

.main .titleGroup {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2258A2;
  color: #fff;
  height: 150px;
}

.main .productsGroup {
  display: flex;
  flex-direction: column;
}

.main .productsGroup .group {
  height: 500px;
  width: 100%;
  display: flex;
}

.main .productsGroup .group .img {
  width: 50%;
  height: 100%;
  position: relative;
}

.main .productsGroup .group .img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.main .productsGroup .group .img .filter {
  background-color: rgba(34, 87, 162, 0.658);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}

.main .productsGroup .group .text-content {
  width: 50%;
  height: 100%;
}

.main .productsGroup .group .text-content h1 {
  color: #34495E;
  font-weight: bold;
  margin: 50px;
}

.main .productsGroup .group .text-content p {
  margin: 50px;
  color: #34495E;
  font-weight: 600;
}

.main .ourAdvantages {
  margin-bottom: 50px;
}

.main .ourAdvantages h1 {
  margin: 55px 0;
  text-align: center;
  color: #34495E;
}

.main .ourAdvantages .content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.main .ourAdvantages .content .card {
  width: 550px;
  min-height: 210px;
  box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.548);
  margin: 15px 0;
  border-radius: 5px;
}

.main .ourAdvantages .content .card .row {
  display: flex;
  align-items: center;
  margin: 20px;
}

.main .ourAdvantages .content .card .row .title {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 18px;
  color: #2258A2;
  margin-left: 20px;
}

.main .ourAdvantages .content .card .row .arr {
  height: 50px;
  background-color: #2258A2;
  width: 2px;
}

.main .ourAdvantages .content .card .row .mark {
  width: 50px;
  height: 50px;
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main .ourAdvantages .content .card .row .mark img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.main .ourAdvantages .content .card .text {
  margin: 50px;
  margin-top: 0;
  margin-left: 20px;
  color: #465057;
  font-weight: bold;
  line-height: 25px;
}

.footer {
  width: 100%;
  background-color: #333334;
  min-height: 100px;
  color: #fff;
}

.footer_nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.footer_nav ul li {
  padding-right: 15px;
}

.footer_nav ul li:not(:first-child) {
  padding-left: 15px;
}

.footer_nav ul li:not(:last-child) {
  border-right: 2px solid #fff;
}

.footer .group {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 25px;
}

.footer .group-2 {
  display: flex;
  align-items: center;
}

.footer .group-2 .allRR {
  text-align: center;
}

.footer .group-2 .orderCall {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 10px 25px;
  transition: all 0.3s ease-in;
  cursor: pointer;
}

.footer .group-2 .orderCall svg {
  margin-right: 10px;
}

.footer .group-2 .orderCall .st0 {
  fill: #fff;
}

.footer .group-2 .orderCall:hover {
  background-color: #fff;
  color: #333334;
}

.footer .group-2 .orderCall:hover .st0 {
  transition: all 0.3s ease-in;
  fill: #333334;
}

@media screen and (max-width: 729px) {
  .footer_nav ul li {
    border-right: none !important;
    padding: 0 !important;
    padding: 10px !important;
  }
  .footer .orderCall {
    margin-bottom: 15px !important;
  }
  .footer .group {
    justify-content: center !important;
    padding: 15px;
  }
  .main .productsGroup .text-content {
    text-align: center;
  }
  .main .productsGroup .text-content h1 {
    margin: 20px !important;
  }
  .main .productsGroup .text-content p {
    margin: 20px !important;
  }
}

@media screen and (max-width: 1130px) {
  .main .content .container {
    justify-content: center;
  }
  .main .productsGroup .group {
    height: 100%;
    min-height: 500px;
  }
  .main .productsGroup .group .img {
    width: 100%;
    height: 600px;
  }
  .main .productsGroup .group .text-content {
    width: 100%;
    min-height: 600px;
  }
  .main .productsGroup .group-right {
    flex-direction: column;
  }
  .main .productsGroup .group-left {
    flex-direction: column-reverse;
  }
}

@media screen and (max-width: 945px) {
  .header .container {
    flex-wrap: wrap;
  }
  .header-nav {
    display: none;
  }
  .header_mobile_btn {
    display: flex;
  }
  .header_language {
    display: none;
  }
}
